import { h, ref, reactive, onMounted } from "vue";
import { ElNotification } from "element-plus";
import 'element-plus/es/components/notification/style/index'

import axios from "../../../shared/axios.js";
export default {
  props: {
    isUrgency: {
      type: Boolean,
      required: false,
      default: false
    },
    isAdministrator: {
      type: Boolean,
      required: false,
      default: false
    },
    table: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    }
  },
  setup: function setup(props) {
    var tableData = ref([]);

    var handleError = function handleError() {
      ElNotification({
        title: "Ошибка",
        message: h("i", {
          style: "color: teal"
        }, "\u041E\u0448\u0438\u0431\u043A\u0430 \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0435 ".concat(props.label)),
        type: "error"
      });
    };

    var getTableData = function getTableData() {
      axios.get("/dashboard/control/".concat(props.table)).then(function (response) {
        tableData.value = response.data;
      }).catch(function () {
        handleError();
      });
    };

    var deleteRow = function deleteRow(index) {
      axios.delete("/dashboard/control/".concat(props.table), {
        data: {
          id: tableData.value[index].value
        }
      }).then(function () {
        getTableData();
        ElNotification({
          title: "Успешно удалено",
          message: h("i", {
            style: "color: teal"
          }, "\u0423\u0434\u0430\u043B\u0435\u043D\u043E \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0435 ".concat(props.label)),
          type: "success"
        });
      }).catch(function () {
        handleError();
      });
    };

    var toggleEdit = function toggleEdit(index) {
      tableData.value[index].edit = !tableData.value[index].edit;
      tableData.value[index].editValue = tableData.value[index].label;
      tableData.value[index].editInterval = tableData.value[index].interval;
    };

    var editRow = function editRow(index) {
      var data = {
        id: tableData.value[index].value,
        field: tableData.value[index].editValue
      };

      if (props.isUrgency) {
        data.interval = tableData.value[index].editInterval;
      }

      axios.patch("/dashboard/control/".concat(props.table), data).then(function () {
        getTableData();
        ElNotification({
          title: "Успешно изменено",
          message: h("i", {
            style: "color: teal"
          }, "\u0417\u0430\u043C\u0435\u043D\u0435\u043D\u043E \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0435 ".concat(props.label)),
          type: "success"
        });
      }).catch(function () {
        handleError();
      });
    };

    var formRef = ref(null);
    var addForm = reactive({
      value: null,
      interval: null
    });
    var rules = {
      value: [{
        required: true,
        message: "Пожалуйста, укажите значение",
        trigger: "blur"
      }],
      interval: [{
        required: true,
        message: "Пожалуйста, укажите интервал",
        trigger: "blur"
      }]
    };

    var addRow = function addRow() {
      formRef.value.validate(function (valid) {
        if (valid) {
          var data = props.isUrgency ? {
            field: addForm.value,
            interval: addForm.interval
          } : {
            field: addForm.value
          };
          axios.post("/dashboard/control/".concat(props.table), data).then(function () {
            getTableData();
            ElNotification({
              title: "Успешно добавлено",
              message: h("i", {
                style: "color: teal"
              }, "\u0414\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u043E \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0443 ".concat(props.label)),
              type: "success"
            });
          }).catch(function () {
            handleError();
          });
          return true;
        }

        return false;
      });
    };

    onMounted(function () {
      getTableData();
    });
    return {
      tableData: tableData,
      deleteRow: deleteRow,
      toggleEdit: toggleEdit,
      editRow: editRow,
      formRef: formRef,
      addForm: addForm,
      rules: rules,
      addRow: addRow
    };
  }
};